.globusBlock {
    position: relative;
    width: 100%;
    margin: 0px;
    padding: 0px;
}
.quote{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.info{
    margin-top: 10%;
    padding: 5px;
    hyphens: auto;
    
    p:nth-child(1){
        text-align: center;
        text-decoration: underline 1px #00e5ff;
    }
    p{
        font-size: 1.2rem;
        line-height: 1.3rem;
        font-weight: 400;
        letter-spacing: 1.8px;
        text-indent: 1rem;
        hyphens: auto;
        word-break:break-all;

    }
}
@media (max-width: 3500px) {
    .globusBlock {
        height: 1120px;
    }
}
@media (max-width: 2500px) {
    .globusBlock {
        height: 950px;
    }
}
@media (max-width: 1901px) {
    .globusBlock {
        height: 930px;
    }
}
@media (max-width: 1401px) {
